a {
  color: #00DCFF;
}

a:hover, a:focus {
  color: #0396ad;
}

footer p {
  font-size: 20px;
}

.btn-default {
  color: #00DCFF;
  border: 1px solid #00DCFF;
  background-color: transparent;
}

.btn-default:focus, .btn-default:hover {
  border: 1px solid #00DCFF;
  background-color: #00DCFF;
}

img.img-v-logo {
  width: 50%;
}

.gradientoverlay {
  background: -moz-linear-gradient(to bottom right, rgba(0, 47, 75, 0.5) 0%, #dc4225 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(to bottom right, rgba(0, 47, 75, 0.5) 0%, #dc4225 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom right, rgba(0, 47, 75, 0.5) 0%, #dc4225 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80002f4b', endColorstr='#dc4225',GradientType=0 );
}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
}

.content-section {
  padding-top: 100px;
  padding-bottom: 150px;
}

.container.content-section {
  padding-top: 200px;
}

@media (min-width: 992px) {
  .content-section {
    padding-top: 100px;
    padding-bottom: 200px;
  }
  .container.content-section {
    padding-top: 150px;
  }
}

#info {
  background-image: url("../img/circuit-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.card {
  border: none;
  border-radius: none;
}

.card-text {
  color: #011F27;
}

.row-heading {
  padding-top: 100px;
}
